import React, { PureComponent } from 'react';
import * as Scroll from 'react-scroll';
import CSSModules from 'react-css-modules';
import Scrollspy from 'react-scrollspy';
import classnames from 'classnames';

import menuList from '../data';
import styles from './DesktopMenu.scss';
import checkSafari from '_utils/checkSafari';

@CSSModules(styles, { allowMultiple: true })
export default class DesktopMenu extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      left: 0,
      width: 0,
      isSafari: false,
    };
  }

  componentDidMount() {
    this.switchOs();
  }

  switchOs = () => {
    if (checkSafari()) {
      this.setState({
        isSafari: true,
      });
    }
  }

  scrollTo = (nameAnchor) => {
    const { isSafari } = this.state;
    switch (nameAnchor) {
      case 'about':
        this.setState({ left: 1, width: 59 });
        break;
      case 'team':
        if (isSafari) {
          this.setState({ left: 111.5, width: 46 });
        } else {
          this.setState({ left: 110, width: 45 });
        }
        break;
      case 'job':
        if (isSafari) {
          this.setState({ left: 207.5, width: 33 });
        } else {
          this.setState({ left: 204, width: 32 });
        }
        break;
      default:
        this.setState({ left: 0, width: 0 });
    }
  }

  handleUpdate = (node) => {
    this.scrollTo(node ? node.id : '');
  }

  handleClick = ({ currentTarget }) => {
    this.scrollTo(currentTarget.dataset.id);
  }

  render() {
    const { left, width, isSafari } = this.state;

    return (
      <div styleName={classnames('root', { isSafari })}>
        <Scrollspy
          items={['what', 'about', 'team', 'job']}
          offset={-100}
          onUpdate={this.handleUpdate}
        >
          {
            menuList.map(item => (
              <Scroll.Link
                key={item.href}
                to={item.href}
                spy={false}
                smooth="easeInOutQuad"
                offset={-75}
                duration={700}
                data-id={item.href}
              >
                {item.label}
              </Scroll.Link>
            ))
          }
        </Scrollspy>
        <div
          styleName={left ? 'scrollSpy in' : 'scrollSpy'}
          style={{ left, width }}
        />
      </div>
    );
  }
}
