import React, { Component } from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';
import { reduxForm, Field, getFormSyncErrors } from 'redux-form';
import { connect } from 'react-redux';
import classnames from 'classnames';

import Button from '_components/Button';
import RenderField from '_components/RenderField';
import { sendFormContactUs } from '_actions/form';
import { contactUsValidation } from '_utils/validateForm';
import { changeErrorMessage, resetErrorMessage } from '_actions/errors';
import { errorSelector } from '_selectors';
import { trimField } from '_utils/normalizeForm';
import styles from './ContentContact.scss';

import { FORM_NAME_CONTACT_US as FORM_NAME } from '_constants/form';

@CSSModules(styles, { allowMultiple: true })
class ContentContactContainer extends Component {
  state = {
    bluredField: 'message',
  };

  componentDidMount() {
    const ua = navigator.userAgent.toLowerCase();
    this.isAndroid = ua.indexOf('android') > -1;
    if (this.isAndroid) {
      document.addEventListener('touchmove', this.handleTouchMove, { passive: false });
    }
  }

  componentWillUnmount() {
    if (this.isAndroid) {
      document.removeEventListener('touchmove', this.handleTouchMove);
    }
  }

  handleTouchMove = () => {
    if (this.focusedInput) {
      this.focusedInput.blur();
    }
  }

  handleClick = () => {
    const { sendErrorMessage, formSyncErrors } = this.props;
    const { bluredField } = this.state;

    if (formSyncErrors) {
      const value = formSyncErrors[bluredField];
      if (!value) {
        const values = Object.values(formSyncErrors);
        const keys = Object.keys(formSyncErrors);
        const sendedObject = {
          message: values[0],
          from: FORM_NAME,
          fieldName: keys[0],
        };
        sendErrorMessage(sendedObject);
      } else {
        const sendedObject = {
          message: value,
          from: FORM_NAME,
          fieldName: bluredField,
        };

        sendErrorMessage(sendedObject);
      }
    }
  }

  handleFocus = ({ target }) => {
    if (this.props.errorObject.message) {
      this.props.resetError('');
    }
    this.focusedInput = target;
  }

  handleBlur = (evt) => {
    this.setState({
      bluredField: evt.target.name,
    });
  }

  render() {
    const {
      className,
      handleSubmit,
      errorObject,
    } = this.props;

    const errored = errorObject.from === FORM_NAME && Boolean(errorObject.message);

    return (
      <div className={className} styleName="root">
        <h2 className="headingH2" styleName="title">Contact Us</h2>
        <form
          styleName="form"
          onSubmit={handleSubmit}
        >
          <Field
            className={classnames(
              styles.textarea,
              { [styles.errored]: errored && errorObject.fieldName === 'message' },
            )}
            element="textarea"
            placeholder="Your message"
            name="message"
            component={RenderField}
            onFocus={this.handleFocus}
            onBlur={this.handleBlur}
          />

          <Field
            className={classnames(
              styles.input,
            )}
            element="input"
            placeholder="Name"
            name="name"
            component={RenderField}
            onFocus={this.handleFocus}
          />

          <Field
            className={classnames(
              styles.input,
              { [styles.errored]: errored && errorObject.fieldName === 'email' },
              styles.lastInput,
            )}
            element="input"
            placeholder="email"
            name="email"
            component={RenderField}
            onFocus={this.handleFocus}
            onBlur={this.handleBlur}
            normalize={trimField}
          />

          <Button
            className={styles.button}
            label="Send"
            type="submit"
            onClick={this.handleClick}
          />
        </form>
      </div>
    );
  }
}

ContentContactContainer.propTypes = {
  className: PropTypes.string,
  errorObject: PropTypes.object,
  handleSubmit: PropTypes.func,
  sendErrorMessage: PropTypes.func,
  resetError: PropTypes.func,
  formSyncErrors: PropTypes.object,
};


const mapStateToProps = state => ({
  formSyncErrors: getFormSyncErrors(FORM_NAME)(state),
  errorObject: errorSelector(state),
});

const mapDispatchToProps = dispatch => ({
  onSubmit() {
    dispatch(sendFormContactUs());
  },
  sendErrorMessage(message) {
    dispatch(changeErrorMessage(message));
  },
  resetError() {
    dispatch(resetErrorMessage());
  },
});


export default connect(mapStateToProps, mapDispatchToProps)(reduxForm({
  form: FORM_NAME,
  validate: contactUsValidation,
})(ContentContactContainer));
