import errorsCopyright from '_constants/errors';

export const emailWithAttachments = (values) => {
  const errors = {};

  if (!values.email) {
    errors.email = errorsCopyright.emptyEmail;
  } else if (!/^[A-Z0-9А-Я._%+-]+@[A-Z0-9А-Я.-]+\.[A-ZА-Я]{2,10}$/i.test(values.email)) {
    errors.email = errorsCopyright.invalidEmail;
  }

  if (!values.attachments || values.attachments.length === 0) {
    errors.attachments = errorsCopyright.emptyCv;
  }

  return errors;
};

