export const contactUsValidation = (values) => {
  const errors = {};

  if (!values.message) {
    errors.message = errorsCopyright.emptyMessage;
  }

  if (!values.email) {
    errors.email = errorsCopyright.emptyEmail;
  } else if (!/^[A-Z0-9А-Я._%+-]+@[A-Z0-9А-Я.-]+\.[A-ZА-Я]{2,10}$/i.test(values.email)) {
    errors.email = errorsCopyright.invalidEmail;
  }

  return errors;
};
