import React, { PureComponent } from 'react';
import CSSModules from 'react-css-modules';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { successStatusFormSelector } from '_selectors';
import { clearFormStatus } from '_actions/form';
import delay from 'lodash/delay';

import Popup from '_components/Popup';
import ContentContact from '_components/ContentContact';
import ContentPrivacy from './ContentPrivacy';
import ContentThanks from '_components/ContentThanks';

import styles from './Footer.scss';

@CSSModules(styles, { allowMultiple: true })
class Footer extends PureComponent {
  state = {
    popupOpenContact: false,
    popupOpenPrivacy: false,
    popupOpenthanks: false,
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.successSend && nextProps.successSend !== this.props.successSend) {
      this.setState({
        popupOpenthanks: true,
      });
      delay(() => {
        this.setState({
          popupOpenContact: false,
          popupOpenPrivacy: false,
        });
      }, 700);

      delay(this.props.clearStatuses, 100);
    }
  }

  handleClickAnchor = name => (event) => {
    event.preventDefault();
    this.setState({ [name]: true });
  }

  handleCloseModal = name => () => {
    this.setState({ [name]: false });
  }

  render() {
    return (
      <footer styleName="root" className="container">
        <div styleName="links">
          <a
            styleName="email"
            target="_self"
            href="mailto:info@novum.is"
          >
            info@novum.is
          </a>
          <a href="/" onClick={this.handleClickAnchor('popupOpenContact')}>Contact us</a>
          <a href="/" onClick={this.handleClickAnchor('popupOpenPrivacy')}>Privacy policy</a>
          <Popup open={this.state.popupOpenContact} onCloseModal={this.handleCloseModal('popupOpenContact')}>
            <ContentContact onCloseModal={this.handleCloseModal('popupOpenContact')} />
          </Popup>
          <Popup open={this.state.popupOpenPrivacy} onCloseModal={this.handleCloseModal('popupOpenPrivacy')}>
            <ContentPrivacy />
          </Popup>
          <Popup open={this.state.popupOpenthanks} onCloseModal={this.handleCloseModal('popupOpenthanks')}>
            <ContentThanks onCloseModal={this.handleCloseModal('popupOpenthanks')} />
          </Popup>
        </div>
        <div styleName="copyright">
          Novum © 2018 All rights reserved
        </div>
      </footer>
    );
  }
}

Footer.propTypes = {
  successSend: PropTypes.bool,
  clearStatuses: PropTypes.func,
};

const mapStateToProps = state => ({
  successSend: successStatusFormSelector(state),
});

const mapDispatchToProps = dispatch => ({
  clearStatuses() {
    dispatch(clearFormStatus());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(Footer);
