import React from 'react';
import CSSModules from 'react-css-modules';
import PropTypes from 'prop-types';
import checkEmodjy from '_utils/checkEmojiSupport';

import Button from '_components/Button';
import styles from './ContentThanks.scss';

let smail = '';

if (!__SERVER__) {
  smail = checkEmodjy() ? '✨' : '';
}

const ContentThanks = ({ onCloseModal }) => (
  <div styleName="root">
    <div styleName="wrap">
      <div styleName="mail-logo"><svg><use xlinkHref="#icon_mail" /></svg></div>
      <h2 className="headingH2">
        You are awesome!<span role="img" aria-label="add">{smail}</span>
      </h2>
      <div styleName="description">
        Thank you! We sent you an email with a confirmation that everything is OK!
      </div>
      <Button
        onClick={onCloseModal}
        label="Ok"
        className={styles.btn}
      />
    </div>
  </div>
);

ContentThanks.propTypes = {
  onCloseModal: PropTypes.func.isRequired,
};

export default CSSModules(styles)(ContentThanks);
