import React, { Component } from 'react';
import PropTypes from 'prop-types';
import uniqueId from 'lodash/fp/uniqueId';
import Dropzone from 'react-dropzone';
import CSSModules from 'react-css-modules';
import styles from './FileUploader.scss';

import dispatchErrorMessage from '_hocs/dispatchErrorMessage';
import DefaultButton from './Button/Default';
import AddButton from './Button/Add';
import UploadedFile from './UploadedFile';

import errorsCopyright from '_constants/errors';

const MAX_FILE_SIZE = 25000000;
const MAX_COUNT_FILES = 3;

import { fileMimeTypes } from '_constants/form';

const errorsMessage = {
  fileMoreMax: errorsCopyright.bigSizeFile,
  overallSize: errorsCopyright.overallSize,
  countFiles: errorsCopyright.countFiles,
};

@dispatchErrorMessage
@CSSModules(styles, { allowMultiple: true })

class FileUploader extends Component {
  static propTypes = {
    className: PropTypes.string,
    input: PropTypes.object,
    changeErrorMessage: PropTypes.func,
    resetErrorMessage: PropTypes.func,
  };

  state = {
    files: [],
  }

  componentWillReceiveProps = (nextProps) => {
    if (nextProps.input.value !== this.props.input.value) {
      this.setState({
        files: nextProps.input.value,
      });
    }
  }

  onDrop = (filesToUpload, rejectFile) => {
    let fileError = '';

    if (rejectFile.length > 0) {
      rejectFile.forEach((item) => {
        fileError = errorsMessage.fileMoreMax.replace(/{value}/i, item.name);
      });
    }

    if (filesToUpload.length > 0) {
      const newFiles = [...this.state.files];

      const oldSize = newFiles.reduce((prev, curr) => prev + curr.content.size, 0);

      const newSize = filesToUpload.reduce((prev, curr) => prev + curr.size, oldSize);

      if ((filesToUpload.length + newFiles.length) > MAX_COUNT_FILES) {
        fileError = errorsMessage.countFiles;
      } else if (newSize < MAX_FILE_SIZE) {
        filesToUpload.forEach((item) => {
          newFiles.push({
            id: uniqueId(),
            content: item,
          });
        });
      } else {
        fileError = errorsMessage.overallSize;
      }

      const { input: { onChange } } = this.props;
      if (newFiles) {
        onChange(newFiles);
      }
    }

    if (fileError) {
      this.props.changeErrorMessage({ message: fileError });
    } else {
      this.props.resetErrorMessage();
    }
  }

  removeImage = (currentId) => {
    const newFiles = this.state.files.filter(item => item.id !== currentId);

    this.setState({
      files: newFiles,
    });

    this.props.input.onChange(newFiles);
  }

  render() {
    const {
      className,
    } = this.props;

    const { files } = this.state;

    return (
      <div className={className} styleName="root">
        {files.length > 0 && (
          <div>
            {files.map(item => (
              <UploadedFile
                key={item.id}
                className={styles.uploaded}
                title={item.content.name}
                onClick={() => { this.removeImage(item.id); }}
              />
            ))}
          </div>
        )}

        <Dropzone
          styleName="root"
          accept={fileMimeTypes}
          maxSize={MAX_FILE_SIZE}
          onDrop={this.onDrop}
          onDropAccepted={this.onDrop}
          multiple
        >
          {files.length === 0 && <DefaultButton />}
          {files.length > 0 && files.length < 3 && (
            <AddButton className={styles.addButton} />
          )}
        </Dropzone>
      </div>
    );
  }
}

export default FileUploader;
