import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import ErrorMessage from '_components/ErrorMessage';
import { viewportSelector, isFixedHeaderSelector } from '_selectors';

import Header from './Header';
import BublikSection from './BublikSection';
import Footer from './Footer';
import JoinSection from './JoinSection';
import TeamSection from './TeamSection';

const mapStateToProps = state => ({
  viewport: viewportSelector(state),
  isOverflowBody: state.common.isOverflowBody,
  isMenuOpen: state.common.isMenuOpen,
  fixedHeader: isFixedHeaderSelector(state),
});

@connect(mapStateToProps)
export default class HomePage extends Component {
  static propTypes = {
    viewport: PropTypes.string.isRequired,
    isOverflowBody: PropTypes.bool.isRequired,
    isMenuOpen: PropTypes.bool.isRequired,
    fixedHeader: PropTypes.bool.isRequired,
  };

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
  }

  componentDidUpdate() {
    const { isOverflowBody } = this.props;

    document.body.style.overflow = isOverflowBody ? 'hidden' : null;
    document.documentElement.style.overflow = isOverflowBody ? 'hidden' : null;
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    document.body.classList.toggle('resize', true);

    setTimeout(() => {
      document.body.classList.toggle('resize', false);
    }, 100);
  }

  render() {
    const { viewport, isMenuOpen, fixedHeader } = this.props;

    return (
      <Fragment>
        <Header isMenuOpen={isMenuOpen} />
        <ErrorMessage />
        <BublikSection fixedHeader={fixedHeader} />
        <TeamSection viewport={viewport} />
        <JoinSection />
        <Footer />
      </Fragment>
    );
  }
}
