import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { ReactTransitionGroup } from '_components/ReactTransitionGroup';

import Item from './Item';

const COUNTER_ANIMATION_DURATION = 200;
const timeoutTime = 5000;

class ErrorMessage extends Component {
  componentWillReceiveProps(nextProps) {
    if (nextProps.openPopup !== this.props.openPopup) {
      this.props.resetErrorMessage();
    }
    this.clearRemoveTimeout();
  }

  shouldComponentUpdate(nextProps) {
    return nextProps.openPopup === this.props.openPopup;
  }

  componentDidUpdate() {
    const { message } = this.props;
    const isVisible = Boolean(message);

    if (isVisible) {
      this.removeAfterTimeout();
    }
  }

  handleCloseClick = () => {
    this.props.resetErrorMessage();
    this.clearRemoveTimeout();
  }

  removeAfterTimeout = () => {
    this.timeout = setTimeout(() => {
      this.handleCloseClick();
    }, timeoutTime);
  }

  clearRemoveTimeout = () => {
    if (this.timeout) {
      clearTimeout(this.timeout);
    }
  }

  render() {
    const { message, openPopup, fixedHeader } = this.props;
    const isVisible = Boolean(message);

    return (
      <ReactTransitionGroup
        transitionKey={isVisible}
        timeout={COUNTER_ANIMATION_DURATION}
      >
        <Item
          message={message}
          isVisibleItem={isVisible}
          onCloseClick={this.handleCloseClick}
          openPopup={openPopup}
          fixedHeader={fixedHeader}
        />
      </ReactTransitionGroup>
    );
  }
}

ErrorMessage.propTypes = {
  message: PropTypes.string,
  resetErrorMessage: PropTypes.func,
  openPopup: PropTypes.bool,
  fixedHeader: PropTypes.bool,
};


export default ErrorMessage;
