/* eslint-disable */
export const getCurrentScale = (defaultScale, finalScale, commonHeight, bublicHeight, scroll) => {
  const heightOneScreen = commonHeight / 2;
  // весь путь анимации
  const fullPath = commonHeight - ((bublicHeight / 2) + (heightOneScreen / 2));

  const maxScale = finalScale - defaultScale;
  const stepScale = maxScale / fullPath;
  const currentScale = defaultScale + stepScale * scroll;

  return currentScale > finalScale ? finalScale : currentScale;
}

export const getOffsetYForBublic = (bublic, secondScreen) => {
  const bublicBottom = bublic.getBoundingClientRect().bottom;
  const secondScreenTop = secondScreen.getBoundingClientRect().top;

  return bublicBottom - secondScreenTop;
}

export const getBarScale = (defaultScale, finalScale, mainScreen, bar, scroll) => {
  const mainScreenHeight = mainScreen.offsetHeight;
  const barHeight = bar.clientHeight;
  const barTop = bar.offsetTop;

  const fullPath = (mainScreenHeight + scroll) - barHeight - barTop;
  const maxScale = finalScale - defaultScale;
  const stepScale = (maxScale / fullPath);
  const currentScale = defaultScale + stepScale * scroll;

  return currentScale > finalScale ? finalScale : currentScale;
};

export const getTopSecondBublik = (screen) => {
  return screen.clientHeight;
};

export const isNeedStartGradient = (about, bublik, gradient) => {
  about.getBoundingClientRect().bottom;
  bublik.getBoundingClientRect().bottom;
  about.clientHeight;
  gradient.clientHeight;
  bublik.clientHeight;
  const shift = (about.clientHeight / 2) - bublik.clientHeight - gradient.clientHeight;

  const currentShift = about.getBoundingClientRect().bottom - bublik.getBoundingClientRect().bottom;

  if (bublik.getBoundingClientRect().bottom + shift > about.getBoundingClientRect().bottom) {
    const sizeGradient = 100 - ((currentShift * 100) / shift);
    return sizeGradient < 100 ? sizeGradient : 100;
  } return false;
};
