import React from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { changeErrorMessage, resetErrorMessage } from '_actions/errors';

const dispatchErrorMessage = WrappedComponent => props => (
  <WrappedComponent {...props} />
);

const composedHoc = compose(
  connect(null, { changeErrorMessage, resetErrorMessage }, null, { pure: false }),
  dispatchErrorMessage,
);

export default composedHoc;
