import React, { PureComponent, Fragment } from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';
import classnames from 'classnames';

import styles from './RenderField.scss';

@CSSModules(styles, { allowMultiple: true })

class RenderField extends PureComponent {
  getCurrentField = (element) => {
    switch (element) {
      case 'textarea':
        return 'textarea';

      default:
        return 'input';
    }
  }

  render() {
    const {
      className,
      input,
      placeholder,
      element,
      defaultStyles,
    } = this.props;

    const CurrentField = this.getCurrentField(element);

    return (
      <Fragment>
        <CurrentField
          className={className}
          styleName={classnames('root', { defaultStyles })}
          {...input}
          placeholder={placeholder}
        />
      </Fragment>
    );
  }
}

RenderField.propTypes = {
  className: PropTypes.string,
  element: PropTypes.string,
  placeholder: PropTypes.string,
  input: PropTypes.object,
  meta: PropTypes.object,
  defaultStyles: PropTypes.bool,
};

RenderField.defaultProps = {
  defaultStyles: true,
};


export default RenderField;
