import { createSelector } from 'reselect';

export const rootSelector = state => state;

export const commonSelector = createSelector(
  rootSelector,
  ({ common }) => common,
);

export const viewportSelector = createSelector(
  commonSelector,
  ({ viewport }) => viewport,
);

export const isOverflowBodySelector = createSelector(
  commonSelector,
  ({ isOverflowBody }) => isOverflowBody,
);

export const isFixedHeaderSelector = createSelector(
  commonSelector,
  ({ headerFixed }) => headerFixed,
);
