import React, { PureComponent } from 'react';
import CSSModules from 'react-css-modules';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import styles from './TeamItem.scss';

@CSSModules(styles, { allowMultiple: true })
export default class TeamItem extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    item: PropTypes.shape({
      img1x: PropTypes.string,
      img2x: PropTypes.string,
      name: PropTypes.string,
      post: PropTypes.string,
    }).isRequired,
  }

  state = {
    hover: false,
  }

  onTouch = hover => () => {
    this.setState({ hover });
  }

  render() {
    const {
      img1x,
      img2x,
      name,
      post,
    } = this.props.item;

    const { className } = this.props;

    const defaultClass = this.state.hover ? 'root hover' : 'root';

    return (
      <div
        className={className}
        styleName={classnames(defaultClass)}
        onTouchStart={this.onTouch(true)}
        onTouchEnd={this.onTouch(false)}
      >
        <picture>
          <source srcSet={img1x} media="(max-width: 700px)" />
          <source srcSet={img2x} />
          <img srcSet={img2x} alt={name} />
        </picture>
        <div styleName="description">
          <span>{name}</span>
          <span>{post}</span>
        </div>
      </div>
    );
  }
}
