import React from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';
import classnames from 'classnames';

import styles from './Bar.scss';

const Bar = ({
  className,
  active,
  img1x,
  img2x,
}) => (
  <div
    className={className}
    styleName={classnames('root', { active })}
  >
    <picture>
      <source srcSet={img1x} media="(max-width: 700px)" width="100%" />
      <source srcSet={`${img1x}, ${img2x} 2x`} width="100%" />
      <img srcSet={img1x} alt="bublik" width="100%" />
    </picture>
  </div>
);

Bar.propTypes = {
  className: PropTypes.string,
  active: PropTypes.bool,
  img1x: PropTypes.string,
  img2x: PropTypes.string,
};


export default CSSModules(styles, { allowMultiple: true })(Bar);
