import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';

import Form from './Form';

import styles from './JoinSection.scss';

@CSSModules(styles, { allowMultiple: true })
export default class JoinSection extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
  }

  render() {
    const { className } = this.props;

    return (
      <section className={className} styleName="root" id="job">
        <div className="container">
          <h2 className="headingH3" styleName="title">
            Join our team
          </h2>
          <div styleName="subTitle">
            Enter your email and attach your CV to join us
          </div>
          <Form />
        </div>
      </section>
    );
  }
}
