import React, { PureComponent } from 'react';
import CSSModules from 'react-css-modules';
import PropTypes from 'prop-types';
import AnimateHeight from 'react-animate-height';
import Animated from '_components/Animated';
import classnames from 'classnames';
import Button from '_components/Button';
import TeamItem from './TeamItem';
import styles from './TeamSection.scss';
import teamList from './data';

const defaulBezier = 'cubic-bezier(0.25, 0, 0, 1)';

const defaultDuration = 500;


@CSSModules(styles, { allowMultiple: true })
export default class TeamSection extends PureComponent {
  static propTypes = {
    viewport: PropTypes.string.isRequired,
  }

  state = {
    teamOpen: false,
    height: 0,
  }

  handleShowMore = () => {
    const { height } = this.state;

    this.setState({ teamOpen: true, height: height === 0 ? 'auto' : 0 });
  }

  render() {
    const { viewport } = this.props;
    let numberCols = 4;

    switch (viewport) {
      case 'FULL': numberCols = 5; break;
      case 'HD':
      case 'DESKTOP': numberCols = 4; break;
      case 'LANDSCAPE': numberCols = 3; break;
      case 'PORTRAIT': numberCols = 2; break;
      case 'MOBILE': numberCols = 1; break;
      default:
    }

    const itemsDefault = teamList.slice(0, numberCols > 1
      ? numberCols * 2
      : numberCols * 4);
    const itemsShowMore = teamList.slice(numberCols > 1
      ? numberCols * 2
      : numberCols * 4, teamList.length);

    const { teamOpen } = this.state;

    return (
      <section id="team" styleName="root">
        <h3 className="headingH3">Team</h3>
        <div styleName="team">
          {
            itemsDefault.map(teamItem => (
              <TeamItem key={`${teamItem.id}-team`} item={teamItem} />
            ))
          }
        </div>
        <AnimateHeight
          duration={defaultDuration}
          easing={defaulBezier}
          height={this.state.height}
        >
          <Animated
            duration={defaultDuration}
            delay={defaultDuration}
            easing={defaulBezier}
            isVisible={teamOpen}
            styleName="team"
          >
            {
              itemsShowMore.map(teamItem => (
                <TeamItem key={teamItem.id} styleName={classnames('teamItem', { teamOpen })} item={teamItem} />
              ))
            }
          </Animated>
        </AnimateHeight>
        {
          !this.state.teamOpen && itemsShowMore.length > 0 && (
            <div styleName="show-more">
              <Button
                className={styles.btn}
                label="Show more"
                onClick={this.handleShowMore}
                icon={<svg><use xlinkHref="#icon_plus" /></svg>}
              />
            </div>
          )
        }
      </section>
    );
  }
}
