import React, { Component } from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';

import throttle from 'lodash/throttle';
import PageLoad from '_components/AnimationWrapper/PageLoad';
import Content from './Content';
import Form from './Form';
import Bar from './Bar';
import MobileBlock from './MobileBlock';

import styles from './MainScreen.scss';

const BEFORE_MOBILE_MEDIA_QUERY = '(max-width: 420px)';


@CSSModules(styles, { allowMultiple: true })
class MainScreen extends Component {
  state = {
    isVisibleForm: true,
    isVisibleFormBlock: true,
  };

  componentDidMount() {
    const mediaQueryBeforeMobile = window.matchMedia(BEFORE_MOBILE_MEDIA_QUERY);
    mediaQueryBeforeMobile.addListener(({ matches }) => {
      if (matches) {
        this.setState({
          isVisibleForm: false,
        });
      } else {
        this.setState({
          isVisibleForm: true,
        });
      }
    });
    if (mediaQueryBeforeMobile.matches) {
      // eslint-disable-next-line react/no-did-mount-set-state
      this.setState({
        isVisibleForm: false,
      });
    }

    window.addEventListener('scroll', throttle(this.handleResize, 100));
    window.addEventListener('resize', throttle(this.handleResize, 100));
  }

  handleResize = () => {
    const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
    const height = window.innerHeight;
    let coords = 0;
    if (this.state.isVisibleForm) {
      coords = this.desktopForm.getBoundingClientRect().bottom;
    } else {
      coords = this.mobileForm.getBoundingClientRect().bottom;
    }
    const differentBetween = height - coords;
    if (scrollTop === 0 && !this.state.isVisibleFormBlock) {
      this.setState({ isVisibleFormBlock: true });
    } else if (scrollTop < 10 && !this.state.isVisibleFormBlock) {
      this.setState({ isVisibleFormBlock: true });
    } else if (differentBetween < 50 && !this.state.isVisibleFormBlock) {
      this.setState({ isVisibleFormBlock: true });
    } else if (scrollTop > 10 && differentBetween > 50 && this.state.isVisibleFormBlock) {
      this.setState({ isVisibleFormBlock: false });
    }
  }

  desktopFormRef = (section) => {
    this.desktopForm = section;
  }

  mobileFormRef = (section) => {
    this.mobileForm = section;
  }

  render() {
    const { isVisibleForm, isVisibleFormBlock } = this.state;

    const {
      className,
      innerRef,
      children,
      currentSlide,
      slides,
      firstBarRef,
      secondBarRef,
    } = this.props;

    return (
      <div
        className={className}
        styleName="root"
        ref={innerRef}
        id="what"
      >
        <div
          className="container"
          styleName="wrapper"
        >
          <Content
            className={styles.content}
            currentSlide={currentSlide}
            slides={slides}
          />
          {isVisibleForm ? (
            <PageLoad
              duration={400}
              isVisible={isVisibleFormBlock}
              className={styles.form}
            >
              <Form innerRef={this.desktopFormRef} />
            </PageLoad>
          ) : (
            <PageLoad
              duration={400}
              isVisible={isVisibleFormBlock}
              className={styles.form}
            >
              <MobileBlock innerRef={this.mobileFormRef} />
            </PageLoad>
          )}
        </div>
        <div styleName="stickyWrapper">
          <div
            styleName="sticky"
            ref={firstBarRef}
          >
            {slides.map((slide, index) => (
              <Bar
                key={slide.id}
                className={styles.second}
                active={currentSlide === index}
                img1x={slide.bar1img1x}
                img2x={slide.bar1img2x}
              />
            ))}
          </div>
        </div>
        <div styleName="bublikWrapper">
          <div styleName="likeText" />
          {children}
        </div>
        <div styleName="stickyWrapper">
          <div
            styleName="sticky"
            ref={secondBarRef}
          >
            {slides.map((slide, index) => (
              <Bar
                key={slide.id}
                className={styles.first}
                active={currentSlide === index}
                img1x={slide.bar2img1x}
                img2x={slide.bar2img2x}
              />
            ))}
          </div>
        </div>
        <div styleName="gradient" />
      </div>
    );
  }
}

MainScreen.propTypes = {
  className: PropTypes.string,
  innerRef: PropTypes.func,
  children: PropTypes.node,
  currentSlide: PropTypes.number,
  slides: PropTypes.array,
  firstBarRef: PropTypes.func,
  secondBarRef: PropTypes.func,
};


export default MainScreen;
