import {
  SWITCH_VIEWPORT,
  CHANGE_OVERFLOW_BODY,
  CHANGE_STATUS_MENU,
  CHANGE_HEADER_FIXED,
} from './constants/common';

export const switchViewport = viewport => ({
  type: SWITCH_VIEWPORT,
  payload: { viewport },
});

export const changeOverflowBody = isOverflowBody => ({
  type: CHANGE_OVERFLOW_BODY,
  payload: { isOverflowBody },
});

export const changeStatusMenu = isMenuOpen => ({
  type: CHANGE_STATUS_MENU,
  payload: { isMenuOpen },
});

export const changeHeaderFixed = ({ fixed }) => ({
  type: CHANGE_HEADER_FIXED,
  payload: fixed,
});
