import { createSelector } from 'reselect';
import { rootSelector } from './common';

export const formContentSelector = createSelector(
  rootSelector,
  ({ formContent }) => formContent,
);

export const cvFormSelector = createSelector(
  rootSelector,
  ({ form }) => form.cv || {},
);

export const mainScreenFormSelector = createSelector(
  rootSelector,
  ({ form }) => form.mainScreen || {},
);

export const contactUsFormSelector = createSelector(
  rootSelector,
  ({ form }) => form.contactUs || {},
);

export const subscribeFormSelector = createSelector(
  rootSelector,
  ({ form }) => form.subscribe || {},
);
