import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';

import styles from './Burger.scss';

@CSSModules(styles, { allowMultiple: true })
export default class Burger extends PureComponent {
  static propTypes = {
    status: PropTypes.bool,
    onClick: PropTypes.func.isRequired,
  };

  componentDidMount() {
    this.changedStatus = true;
  }

  changedStatus = false;

  render() {
    const { status, onClick } = this.props;
    const classClosed = this.changedStatus ? 'closed' : '';

    return (
      <button styleName="root" onClick={onClick}>
        <div styleName={(status) ? 'open' : classClosed}>
          <div styleName="top icon" />
          <div styleName="middle icon" />
          <div styleName="bottom icon" />
        </div>
      </button>
    );
  }
}
