import {
  SUCCESS_FORM_SEND,
  CLEAR_FORM_STATUSES,
  FAIL_FORM_SEND,
  LOADING_FORM,
} from '_actions/constants/form';

const defaultState = {
  successFormSend: false,
  failSendForm: false,
  loadingForm: false,
};

const initialState = {
  successFormSend: false,
  failSendForm: false,
  loadingForm: true,
};

export default (state = initialState, action) => {
  const { type } = action;

  switch (type) {
    case SUCCESS_FORM_SEND:
      return {
        ...state,
        successFormSend: true,
        failSendForm: false,
        loadingForm: false,
      };

    case LOADING_FORM:
      return { ...state, loadingForm: true };

    case FAIL_FORM_SEND:
      return {
        ...state,
        failSendForm: true,
        successFormSend: false,
        loadingForm: false,
      };

    case CLEAR_FORM_STATUSES:
      return { ...state, ...defaultState };

    default:
      return state;
  }
};
