import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';

import CrossButton from '_components/CrossButton';
import slashFileName from '_utils/slashFileName';
import styles from './UploadedFile.scss';

const MAX_LENGTH_TITLE = 10;

@CSSModules(styles, { allowMultiple: true })
class UploadedFile extends PureComponent {
  render() {
    const { className, title, onClick } = this.props;
    let fileName = title;

    if (title.length > MAX_LENGTH_TITLE) {
      fileName = slashFileName(title);
    }

    return (
      <div className={className} styleName="root">
        <div styleName="title">
          {fileName}
        </div>
        <CrossButton
          className={styles.cross}
          onClick={onClick}
        />
      </div>
    );
  }
}

UploadedFile.propTypes = {
  className: PropTypes.string,
  title: PropTypes.string,
  onClick: PropTypes.func,
};


export default UploadedFile;
