import {
  SWITCH_VIEWPORT,
  CHANGE_OVERFLOW_BODY,
  CHANGE_STATUS_MENU,
  CHANGE_HEADER_FIXED,
} from '_actionConstants/common';

import { FULL } from '_constants';

const initialState = {
  viewport: FULL,
  isOverflowBody: false,
  isMenuOpen: false,
  headerFixed: false,
};

export default (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case SWITCH_VIEWPORT:
    case CHANGE_OVERFLOW_BODY:
    case CHANGE_STATUS_MENU:
      return { ...state, ...payload };

    case CHANGE_HEADER_FIXED:
      return { ...state, headerFixed: payload };

    default:
      return state;
  }
};
