import React, { Component } from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';
import { connect } from 'react-redux';

import Popup from '_components/Popup';
import ContentSubscribe from '_components/ContentSubscribe';
import Button from '_components/Button';
import { successStatusFormSelector } from '_selectors';

import styles from './MobileBlock.scss';

@connect(
  state => ({
    successSend: successStatusFormSelector(state),
    state,
  }),
)
@CSSModules(styles, { allowMultiple: true })

class MobileBlock extends Component {
  state = {
    isVisiblePopup: false,
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.successSend && nextProps.successSend !== this.props.successSend) {
      this.setState({
        isVisiblePopup: false,
      });
    }
  }

  handleClickNotify = () => {
    this.setState({ isVisiblePopup: true });
  }

  handleCloseModal = () => {
    this.setState({ isVisiblePopup: false });
  }

  render() {
    const { className, innerRef } = this.props;

    return (
      <form
        className={className}
        styleName="root"
        ref={innerRef}
      >
        <div styleName="upperTitle">
          Coming soon
        </div>
        <h4 className="headingH3" styleName="title">
          Easy SEPA payments
        </h4>
        <div styleName="wrapper">
          <Button
            className={styles.button}
            label="Notify me"
            type="button"
            onClick={this.handleClickNotify}
          />
          <Popup open={this.state.isVisiblePopup} onCloseModal={this.handleCloseModal}>
            <ContentSubscribe onCloseModal={this.handleCloseModal} />
          </Popup>
        </div>
      </form>
    );
  }
}

MobileBlock.propTypes = {
  className: PropTypes.string,
  successSend: PropTypes.bool,
  innerRef: PropTypes.func,
};


export default MobileBlock;
