import React, { PureComponent } from 'react';
import CSSModules from 'react-css-modules';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import * as Scroll from 'react-scroll';
import delay from 'lodash/delay';

import { successStatusFormSelector, isFixedHeaderSelector } from '_selectors';
import { changeHeaderFixed } from '_actions/common';

import Popup from '_components/Popup';
import Button from '_components/Button';
import ContentSubscribe from '_components/ContentSubscribe';
import MainMenu from './MainMenu';
import styles from './Header.scss';

@connect(
  state => ({
    successSend: successStatusFormSelector(state),
    fixed: isFixedHeaderSelector(state),
  }),
  { changeHeaderFixed },
)

@CSSModules(styles, { allowMultiple: true })
export default class Header extends PureComponent {
  static propTypes = {
    isMenuOpen: PropTypes.bool.isRequired,
    successSend: PropTypes.bool,
    fixed: PropTypes.bool,
    changeHeaderFixed: PropTypes.func,
  }

  state = {
    popup: false,
  };

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    document.addEventListener('touchmove', this.handleTouchMove, { passive: false });
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.successSend && nextProps.successSend !== this.props.successSend) {
      delay(() => {
        this.setState({
          popup: false,
        });
      }, 700);
    }
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll);
    document.removeEventListener('touchmove', this.handleTouchMove);
  }

  isFixed = false;

  handleScroll = () => {
    if (window.scrollY <= 0 && this.isFixed) {
      this.isFixed = false;
      this.props.changeHeaderFixed({ fixed: false });
    } else if (window.scrollY > 0 && !this.isFixed) {
      this.isFixed = true;
      this.props.changeHeaderFixed({ fixed: true });
    }
  }

  handleTouchMove = (event) => {
    const { isMenuOpen } = this.props;

    if (isMenuOpen) {
      event.preventDefault();
    }
  }

  scrollToTop = (event) => {
    event.preventDefault();
    Scroll.animateScroll.scrollToTop();
  }

  handleClickLogin = () => {
    this.setState({ popup: true });
  }

  handleCloseModal = () => {
    this.setState({ popup: false });
  }

  render() {
    const { isMenuOpen, fixed } = this.props;

    return (
      <header styleName={fixed ? 'root fixed' : 'root'} className="container">
        <div styleName="logotype">
          {
            !isMenuOpen && (
              <a onClick={this.scrollToTop} href="/">
                <svg><use xlinkHref="#icon_logo" /></svg>
              </a>
            )
          }
        </div>
        <div styleName="nav">
          <MainMenu {...this.props} />
          <Button
            onClick={this.handleClickLogin}
            label="Login"
            styleType="login"
            className={styles.login}
          />
          <Popup open={this.state.popup} onCloseModal={this.handleCloseModal}>
            <ContentSubscribe onCloseModal={this.handleCloseModal} />
          </Popup>
        </div>
      </header>
    );
  }
}
