import React, { PureComponent } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import { changeErrorMessage, resetErrorMessage } from '_actions/errors';
import {
  errorMessageSelector,
  isOverflowBodySelector,
  isFixedHeaderSelector,
} from '_selectors';

import ErrorMessage from './ErrorMessage';

@connect(
  state => ({
    message: errorMessageSelector(state),
    openPopup: isOverflowBodySelector(state),
    fixedHeader: isFixedHeaderSelector(state),
  }),
  dispatch => bindActionCreators({
    changeErrorMessage,
    resetErrorMessage,
  }, dispatch),
)

class ContainerErrorMessage extends PureComponent {
  render() {
    const { message, openPopup, fixedHeader } = this.props;

    return (
      <ErrorMessage
        message={message}
        changeErrorMessage={this.props.changeErrorMessage}
        resetErrorMessage={this.props.resetErrorMessage}
        openPopup={openPopup}
        fixedHeader={fixedHeader}
      />
    );
  }
}

ContainerErrorMessage.propTypes = {
  message: PropTypes.string,
  openPopup: PropTypes.bool,
  changeErrorMessage: PropTypes.func,
  resetErrorMessage: PropTypes.func,
  fixedHeader: PropTypes.bool,
};


export default ContainerErrorMessage;
