import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { reduxForm, Field, getFormSyncErrors } from 'redux-form';
import { connect } from 'react-redux';
import CSSModules from 'react-css-modules';
import classnames from 'classnames';

import { sendFormCv } from '_actions/form';
import { changeErrorMessage, resetErrorMessage } from '_actions/errors';
import FileUploader from '_components/FileUploader';
import Button from '_components/Button';
import { emailWithAttachments } from '_utils/validateForm';
import { errorMessageSelector, errorFromSelector } from '_selectors';

import { trimField } from '_utils/normalizeForm';
import RenderField from '_components/RenderField';

import styles from './Form.scss';

import { FORM_NAME_CV as FORM_NAME } from '_constants/form';

@CSSModules(styles, { allowMultiple: true })
class Form extends Component {
  state = {
    isFocused: false,
  }

  handleClick = () => {
    const { sendErrorMessage, formSyncErrors } = this.props;

    if (formSyncErrors) {
      const values = Object.values(formSyncErrors);
      const sendedObject = {
        message: values[0],
        from: FORM_NAME,
      };
      sendErrorMessage(sendedObject);
    }
  }

  handleFocus = () => {
    this.setState({
      isFocused: true,
    });

    if (this.props.errorMessage) {
      this.props.resetError();
    }
  }

  handleBlur = () => {
    this.setState({
      isFocused: false,
    });
  }

  render() {
    const {
      className,
      handleSubmit,
      errorMessage,
      errorFrom,
    } = this.props;

    const { isFocused } = this.state;
    const errored = errorFrom === FORM_NAME && Boolean(errorMessage);


    return (
      <form
        className={className}
        styleName="root"
        onSubmit={handleSubmit}
        ref={(section) => { this.form = section; }}
      >
        <div
          styleName={classnames(
            'field',
            { errored, isFocused },
          )}
          onFocus={this.handleFocus}
          onBlur={this.handleBlur}
        >
          <Field
            className={styles.input}
            component={RenderField}
            element="input"
            placeholder="email"
            name="email"
            defaultStyles={false}
            normalize={trimField}
          />
          <Field
            className={styles.uploader}
            component={FileUploader}
            name="attachments"
          />
        </div>
        <Button
          className={styles.button}
          label="Join"
          type="submit"
          onClick={this.handleClick}
        />
      </form>
    );
  }
}

Form.propTypes = {
  className: PropTypes.string,
  errorMessage: PropTypes.string,
  errorFrom: PropTypes.string,
  handleSubmit: PropTypes.func,
  sendErrorMessage: PropTypes.func,
  resetError: PropTypes.func,
  formSyncErrors: PropTypes.object,
};

const mapStateToProps = state => ({
  formSyncErrors: getFormSyncErrors(FORM_NAME)(state),
  errorMessage: errorMessageSelector(state),
  errorFrom: errorFromSelector(state),
});

const mapDispatchToProps = dispatch => ({
  onSubmit() {
    dispatch(sendFormCv());
  },
  sendErrorMessage(message) {
    dispatch(changeErrorMessage(message));
  },
  resetError() {
    dispatch(resetErrorMessage());
  },
});


export default connect(mapStateToProps, mapDispatchToProps)(reduxForm({
  form: FORM_NAME,
  validate: emailWithAttachments,
})(Form));
