import React from 'react';
import PropTypes from 'prop-types';
import cssModules from 'react-css-modules';

import styles from './TabletMenu.scss';
import menuList from '../data';
import Item from './Item';

@cssModules(styles, { allowMultiple: true })
export default class TabletMenu extends React.Component {
  static propTypes = {
    closed: PropTypes.bool.isRequired,
    onClickMenuItem: PropTypes.func.isRequired,
    onOpenModal: PropTypes.func.isRequired,
    fixed: PropTypes.bool,
  }

  handleClickAnchor = (event) => {
    event.preventDefault();
    this.props.onOpenModal();
  }

  render() {
    const mosilla = navigator.userAgent.search(/Firefox/) > -1;
    const closed = (this.props.closed) ? 'closed' : '';
    const fixed = (this.props.fixed) ? 'fixed' : '';

    return (
      <section
        className="container"
        styleName={`root ${closed} ${fixed}`}
        style={{ height: (mosilla) && '100vh' }}
      >
        <div styleName="list">
          {
            menuList.map(item => (
              <Item
                key={item.href}
                anchor={item.href}
                closed={this.props.closed}
                label={item.label}
                delay={item.delay}
                onClick={this.props.onClickMenuItem}
              />
            ))
          }
        </div>
        <div styleName="links">
          <div styleName="contact">
            <a
              href="/"
              onClick={this.handleClickAnchor}
            >
              Contact us
            </a>
          </div>
          <div styleName={`email ${(this.props.closed) ? 'email-closed' : ''}`}>
            <a
              target="_self"
              href="mailto:info@novum.is"
            >
              info@novum.is
            </a>
          </div>
        </div>
      </section>
    );
  }
}
