import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { changeOverflowBody, changeStatusMenu } from '_actions/common';
import Popup from '_components/Popup';
import ContentContact from '_components/ContentContact';
import DesktopMenu from './DesktopMenu';
import TabletMenu from './TabletMenu';
import Burger from './Burger';

const mapStateToProps = state => ({
  isMenuOpen: state.common.isMenuOpen,
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      changeOverflowBody,
      changeStatusMenu,
    },
    dispatch,
  );

@connect(mapStateToProps, mapDispatchToProps)
export default class MainMenu extends Component {
  static propTypes = {
    isMenuOpen: PropTypes.bool.isRequired,
    changeOverflowBody: PropTypes.func.isRequired,
    changeStatusMenu: PropTypes.func.isRequired,
    fixed: PropTypes.bool,
  }

  state = {
    isMobile: window.innerWidth <= 769,
    popupOpenContact: false,
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
  }

  componentDidUpdate() {
    const { isMenuOpen } = this.props;
    const { isMobile } = this.state;

    if (!isMobile && isMenuOpen) {
      this.handleClickBar();
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize = () => {
    if (!this.mobile && window.innerWidth <= 769) {
      this.mobile = true;
      this.setState({ isMobile: true });
    } else if (this.mobile && window.innerWidth > 769) {
      this.mobile = false;
      this.setState({ isMobile: false });
    }
  }

  handleClickBar = () => {
    const {
      changeOverflowBody: changeOverflow,
      changeStatusMenu: changeMenu,
      isMenuOpen,
    } = this.props;

    changeOverflow(!isMenuOpen);
    changeMenu(!isMenuOpen);
  }

  handleCloseModal = () => {
    this.setState({ popupOpenContact: false });
  }

  handleOpenModal = () => {
    const {
      changeStatusMenu: changeMenu,
      isMenuOpen,
    } = this.props;
    setTimeout(() => {
      changeMenu(!isMenuOpen);
    }, 500);
    this.setState({ popupOpenContact: true });
  }

  render() {
    const { isMenuOpen, fixed } = this.props;
    const { isMobile } = this.state;

    return (
      <nav>
        {isMobile && <Burger status={isMenuOpen} onClick={this.handleClickBar} />}
        {isMobile && isMenuOpen && <TabletMenu
          closed={!isMenuOpen}
          onClickMenuItem={this.handleClickBar}
          onOpenModal={this.handleOpenModal}
          fixed={fixed}
        />}
        {!isMobile && <DesktopMenu />}
        <Popup open={this.state.popupOpenContact} onCloseModal={this.handleCloseModal}>
          <ContentContact />
        </Popup>
      </nav>
    );
  }
}
