import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Modal from './Modal';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';
import ScrollArea from 'react-scrollbar/dist/no-css';
import classnames from 'classnames';

import {
  PORTRAIT,
  MOBILE,
} from '_constants';

import { isFixedHeaderSelector } from '_selectors';

import CrossButton from '_components/CrossButton';
import { changeOverflowBody } from '_actions/common';
import { viewportSelector } from '_selectors/common';
import checkTouch from '_utils/checkTouch';

import styles from './Popup.scss';

const mapStateToProps = state => ({
  viewport: viewportSelector(state),
  fixedHeader: isFixedHeaderSelector(state),
});
const mapDispatchToProps = dispatch => bindActionCreators({
  changeOverflowBody,
}, dispatch);

@connect(mapStateToProps, mapDispatchToProps)
@CSSModules(styles, { allowMultiple: true })
export default class Popup extends Component {
  static propTypes = {
    open: PropTypes.bool,
    onCloseModal: PropTypes.func,
    children: PropTypes.any,
    changeOverflowBody: PropTypes.func.isRequired,
    viewport: PropTypes.string,
    fixedHeader: PropTypes.bool,
  };

  static defaultProps = {
    open: true,
    onCloseModal: () => {},
  }

  state = {
    isTouch: false,
  }

  componentDidMount() {
    document.addEventListener('touchmove', this.handleTouchMove, { passive: false });
    if (checkTouch()) {
      this.takeIsTouchTrue();
    }
  }

  componentWillUnmount() {
    document.removeEventListener('touchmove', this.handleTouchMove);
  }

  setWrapperRef = (node) => {
    this.wrapperRef = node;
  }

  takeIsTouchTrue = () => {
    this.setState({
      isTouch: true,
    });
  }

  handleCloseModal = () => {
    const { onCloseModal } = this.props;

    onCloseModal();
  }

  handleEntered = () => {
    this.props.changeOverflowBody(true);
  }

  handleExited = () => {
    this.props.changeOverflowBody(false);
  }

  handleTouchMove = (event) => {
    const { open } = this.props;

    if (open) {
      if (this.wrapperRef && !this.wrapperRef.contains(event.target)) {
        event.preventDefault();
      }
    }
  }

  render() {
    const {
      open,
      children,
      viewport,
      fixedHeader,
    } = this.props;

    const { isTouch } = this.state;

    return (
      <Modal
        open={open}
        onClose={this.handleCloseModal}
        closeOnOverlayClick={false}
        onEntered={this.handleEntered}
        onExited={this.handleExited}
        styles={{
          modal: { padding: 0, minWidth: '100vw', minHeight: '100vh' },
          overlay: {
            zIndex: 1002,
            overflow: 'hidden',
            padding: 0,
            bottom: 'auto',
            hidden: '100vh',
          },
        }}
        classNames={{
          transitionEnter: 'transition-enter',
          transitionEnterActive: 'transition-enter-active',
          transitionExit: 'transition-exit',
          transitionExitActive: 'transition-exit-active',
        }}
        animationDuration={400}
        showCloseIcon={false}
      >
        <div styleName={classnames('root', { isTouch })} className="simpleText">
          {
            viewport === PORTRAIT || viewport === MOBILE || isTouch ? (
              <div styleName="scrollarea-mobile">
                <CrossButton
                  className={classnames(styles.cross, { [styles.initial]: !fixedHeader })}
                  onClick={this.handleCloseModal}
                  colorSpace="#fff"
                  isBig
                />
                <div styleName="content" ref={this.setWrapperRef}>
                  {children}
                </div>
              </div>
              ) : (
                <ScrollArea
                  speed={1}
                  horizontal={false}
                  vertical
                  verticalScrollbarStyle={{
                    backgroundColor: 'black',
                  }}
                  verticalContainerStyle={{
                    backgroundColor: '#e6e6e6',
                    position: 'absolute',
                    width: '2px',
                    top: '25%',
                    right: '8%',
                    marginRight: '20px',
                    height: '60vh',
                    overflow: 'hidden',
                  }}
                >
                  <CrossButton
                    className={styles.cross}
                    onClick={this.handleCloseModal}
                    colorSpace="#fff"
                    isBig
                  />
                  {children}
                </ScrollArea>
              )
          }
        </div>
      </Modal>
    );
  }
}
