import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';

import styles from './BublikGray.scss';

import grayBublikImage from '_images/bublik/second_state_bublik.png';

@CSSModules(styles, { allowMultiple: true })
class BublikGray extends PureComponent {
  render() {
    const { className, innerRef, bublikGradientRef } = this.props;

    return (
      <div
        className={className}
        styleName="root"
        onClick={this.handleClick}
        ref={innerRef}
      >
        <img
          src={grayBublikImage}
          styleName="gray"
          width="100%"
          alt="grayBublikImage"
        />
        <div
          styleName="gradient"
          ref={bublikGradientRef}
        />
      </div>
    );
  }
}

BublikGray.propTypes = {
  className: PropTypes.string,
  innerRef: PropTypes.func,
  bublikGradientRef: PropTypes.func,
};


export default BublikGray;
