import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import CSSModules from 'react-css-modules';
import defer from 'lodash/defer';

import checkEdge from '_utils/checkEdge';
import MainScreen from './MainScreen';
import AboutScreen from './AboutScreen';
import Bublik from './Bublik';

import {
  getBarScale,
  getCurrentScale,
} from './getDataAnimation';
import dataSlider from './dataSlider';

import styles from './BublikSection.scss';

const BUBLIC_DEFAULT_SCALE = 1;
const BUBLIC_FINAL_SCALE = 1.5;

const BAR_DEFAULT_SCALE = 1;
const BAR_FINAL_SCALE = 1.3;

const INACCURACY_SCALE = 10;

const intervalSlider = 5000;

@CSSModules(styles, { allowMultiple: true })
class BublikSection extends PureComponent {
  state = {
    counter: 0,
  }

  componentDidMount() {
    document.addEventListener('scroll', this.handleScroll);
    const scrollTop = window.pageYOffset || document.documentElement.scrollTop;

    if (scrollTop) {
      defer(this.handleScroll);
      defer(this.handleScroll);
    }

    window.addEventListener('DOMContentLoaded', () => {
      setTimeout(() => {
        this.sliderInterval = setInterval(this.slider, intervalSlider);
      }, 2000);
    });
  }

  componentWillUnmount() {
    clearInterval(this.sliderInterval);
    this.sliderInterval = false;
  }

  slider = () => {
    const { counter } = this.state;

    if (counter < dataSlider.length - 1) {
      this.setState(prev => ({
        counter: prev.counter + 1,
      }));
    } else {
      this.setState({
        counter: 0,
      });
    }
  }

  handleScroll = () => {
    if (checkEdge()) {
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;

      if (scrollTop > 0 && this.sliderInterval) {
        clearInterval(this.sliderInterval);
        this.sliderInterval = false;
      }

      if (scrollTop === 0 && !this.sliderInterval) {
        this.sliderInterval = setInterval(this.slider, intervalSlider);
      }
    } else {
      const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
      const mainScreenHeight = this.mainScreen.clientHeight;
      const aboutScreenHeight = this.aboutScreen.clientHeight;
      const commonHeight = mainScreenHeight + aboutScreenHeight;

      const bublikHeight = this.bublicImage.clientHeight;

      if (scrollTop > 0 && this.sliderInterval) {
        clearInterval(this.sliderInterval);
        this.sliderInterval = false;
      }

      if (scrollTop === 0 && !this.sliderInterval) {
        this.sliderInterval = setInterval(this.slider, intervalSlider);
      }

      const bublikScale = getCurrentScale(
        BUBLIC_DEFAULT_SCALE, BUBLIC_FINAL_SCALE,
        commonHeight,
        bublikHeight,
        scrollTop,
      );

      const barScale = getBarScale(
        BAR_DEFAULT_SCALE,
        BAR_FINAL_SCALE,
        this.mainScreen,
        this.firstBar,
        scrollTop,
      );

      const barOffsetY = (this.firstBar.clientHeight * barScale) - this.firstBar.clientHeight;

      if (!(this.bublicGray.clientWidth * bublikScale >
      this.mainScreen.clientWidth - INACCURACY_SCALE)) {
        this.bublicGray.style.transform = `scale(${bublikScale})`;
        this.bublicImage.style.transform = `scale(${bublikScale})`;
      }

      if (!(this.firstBar.clientWidth * barScale >
      this.mainScreen.clientWidth - INACCURACY_SCALE)) {
        this.firstBar.style.transform = `translate3d(0, -${barOffsetY - 1}px, 0) scale(${barScale})`;
        this.secondBar.style.transform = `translate3d(0, -${barOffsetY - 1}px, 0) scale(${barScale})`;
      } else {
        const scale = (this.mainScreen.clientWidth - INACCURACY_SCALE) / this.firstBar.clientWidth;
        const newBarOffsetY = (this.firstBar.clientHeight * scale) - this.firstBar.clientHeight;

        this.firstBar.style.transform = `translate3d(0, -${newBarOffsetY - 1}px, 0) scale(${scale})`;
        this.secondBar.style.transform = `translate3d(0, -${newBarOffsetY - 1}px, 0) scale(${scale})`;
      }

      if (scrollTop === 0) {
        this.bublicGray.style.transform = 'scale(1)';
        this.bublicImage.style.transform = 'scale(1)';
        this.firstBar.style.transform = 'translate3d(0, 0, 0) scale(1)';
        this.secondBar.style.transform = 'translate3d(0, 0px, 0) scale(1)';
      }
    }
  }

  mainScreenRef = (section) => {
    this.mainScreen = section;
  }

  bublicGrayRef = (section) => {
    this.bublicGray = section;
  }

  bublicImageRef = (section) => {
    this.bublicImage = section;
  }

  aboutScreenRef = (section) => {
    this.aboutScreen = section;
  }

  aboutGradientRef = (section) => {
    this.aboutGradient = section;
  }

  firstBarRef = (section) => {
    this.firstBar = section;
  }

  secondBarRef = (section) => {
    this.secondBar = section;
  }

  bublikGradientRef = (section) => {
    this.bublikGradient = section;
  }

  render() {
    const { className, fixedHeader } = this.props;
    const {
      counter,
    } = this.state;

    return (
      <div
        className={className}
        styleName="root"
        ref={(section) => { this.bublikSectionElement = section; }}
      >
        <MainScreen
          className={styles.main}
          innerRef={this.mainScreenRef}
          barRef={this.barRef}
          currentSlide={counter}
          slides={dataSlider}
          firstBarRef={this.firstBarRef}
          secondBarRef={this.secondBarRef}
          fixedHeader={fixedHeader}
        >
          <Bublik
            className={styles.bublic}
            bublicImageRef={this.bublicImageRef}
            currentSlide={counter}
            slides={dataSlider}
          />
        </MainScreen>
        <AboutScreen
          className={styles.about}
          innerRef={this.aboutScreenRef}
          bublicGrayRef={this.bublicGrayRef}
          bublikClassName={styles.bublic}
          bublikGradientRef={this.bublikGradientRef}
          aboutGradientRef={this.aboutGradientRef}
        />
      </div>
    );
  }
}

BublikSection.propTypes = {
  className: PropTypes.string,
  fixedHeader: PropTypes.bool,
};


export default BublikSection;
